@import "../../styles/utils";

.control-panel {
  display: flex;
  padding: 10px;
  justify-content: space-between;
  box-sizing: border-box;

  position: fixed;
  bottom: 10px;
  left: 10px;
  width: calc(100vw - 20px);
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(10px);
  z-index: 1000;
  border-radius: 24px;

  &__tip {
    position: absolute;
    top: -40px;
    height: 30px;
    padding: 0 10px;
    left: 0;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(10px);
    border-radius: 10px;
    line-height: 30px;
    text-align: center;
  }

  &__glasses {
    width: calc(100% - 80px);
    display: flex;
    border-radius: 20px;
    overflow: auto hidden;

    &::-webkit-scrollbar {
      height: 8px;
    }

    &::-webkit-scrollbar-track {
      background: rgba(0, 0, 0, 0.2);
      border-radius: 5px;
    }

    &::-webkit-scrollbar-thumb {
      background: rgba(0, 0, 0, 0.5);
      border-radius: 5px;
    }

    &::-webkit-scrollbar-thumb:hover {
      background: rgba(0, 0, 0, 0.6);
    }

    .control-panel__button {
      margin-right: 10px;
      background-color: rgba(0, 0, 0, 0.5);
      color: white;

      &:last-child {
        margin-right: 0;
      }

      &_active {
        background-color: rgba(255, 255, 255, 0.5);
        color: black;
      }
    }
  }

  &__button {
    width: 60px;
    height: 60px;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    background-color: white;
    font-size: 32px;
    border-radius: 20px;
    box-shadow: 0 0 32px rgba(0, 0, 0, 0.5);
  }

  &__screenshot {
    margin: 0;
  }

  &__img-preview {
    width: 100%;
    height: auto;
  }
}

.control-panel-new {
  z-index: 1000;

  &__panel-container {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    left: 0;
    bottom: 0;
    width: calc(100% - 20px);
  }

  &__glasses-list {
    display: flex;
    justify-content: start;
    align-items: center;
    margin-bottom: 10px;
    width: 100%;

    overflow: auto;
  }

  &__glasses-item {
    @include gap-margins(0, 10px);
    @include square(60px);

    display: flex;
    flex-shrink: 0;
    background-color: rgba(255, 255, 255, 0.25);
    border: 4px transparent;
    border-radius: $ui-border-radius;

    &_active {
      border-bottom: 4px solid $ui-light-green-color;
    }
  }

  &__glasses-image {
    width: 100%;
    height: auto;
  }

  &__navbar {
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    max-width: 360px;
  }

  &__nav-button {
    @include square($ui-additional-button-size);
    @include noBorder;
    @include normalize-background;

    border-radius: 50%;
    background-color: $light-white-color;
    background-size: 60%;

    transition: scale 0.3s ease-out, box-shadow 0.3s ease-out;

    &:hover {
      transform: scale(1.05);
      box-shadow: 0 0 15px 5px rgba(221, 221, 221, 1);

      transition: scale 0.1s ease-out, box-shadow 0.1s ease-out;
    }

    &:active {
      transform: scale(0.95);
      box-shadow: 0 0 30px 10px rgba(139, 196, 65, 1);
    }

    &_central {
      @include square($ui-main-button-size);
      background-size: 80%;
    }

    &_filter {
      background-image: url("../../images/ui/control-panel/eye-glasses.png");
    }

    &_snapshot {
      background-image: url("../../images/ui/control-panel/snapshot.png");
    }

    &_favourite {
      background-image: url("../../images/ui/control-panel/star-unfilled.png");
    }

    &_is-favourite {
      background-image: url("../../images/ui/control-panel/star-filled.png");
    }
  }

  &__modal-container {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    top: 100vh;
    left: 0;
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.25);
    visibility: hidden;

    transition: top 1s;

    &_is-active {
      top: 0;
      visibility: visible;
    }
  }

  &__modal-content {
    width: 80%;
    height: 80%;
    max-width: 320px;
    max-height: 520px;
    background-color: $light-white-color;
    border-radius: $ui-border-radius;
  }
}
