@import "../../styles/utils";

.edit-container {
  position: relative;
  display: flex;

  &_desktop {
    &:before {
      position: absolute;
      content: '';
      width: 20px;
      height: 100%;
      left: 0;
      background-color: $light-white-color;
      transform: translate(-100%, 0%);
    }
  }

  &__left-sidebar {
    @include box-element;

    flex-grow: 1;
    flex-basis: 700px;
    padding: $ai-main-v-padding $ai-main-h-padding;
    height: calc(100vh - 59px);
    margin-right: 10px;

    background-color: white;
    border-radius: 0 14px 14px 0;
  }

  &__main-content {
    flex-grow: 4;
    flex-basis: 100%;
  }
}
