@import "../../styles/utils";

.edit-glasses {
  &__views-container {
    position: relative;
  }

  &__top-panel {
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: $ai-main-v-padding $ai-main-h-padding;
    z-index: 203;
  }

  &__input {
    @include top-control-box;
  }

  &__button {
    @include top-control-box;
    @include completed(20px, -10px, -10px);
    @include aborted(20px, -10px, -10px);

    position: relative;
  }

  &__input {
    @include top-control-box;
  }

  &__button {
    @include top-control-box;
  }

  &__scene {
    @include box-element;
    @include selectedScene;

    position: relative;
    height: $ai-scene-height;
    margin-bottom: $ai-margin-bottom;
    box-sizing: content-box;
  }

  &__preview-scene {
    @include square(10vh);
    @include box-element;
    @include selectedScene;

    position: absolute;
    bottom: $ai-main-v-padding;
    left: $ai-main-h-padding;
    box-sizing: content-box;
  }

  &__params-container {
    width: 100%;
  }

  &__params-title {
    margin-bottom: 10px;
    font-family: inherit;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }

  &__params-list {
    @include box-element;

    display: flex;
    flex-direction: column;
    padding: $ai-main-v-padding $ai-main-h-padding;
    margin-bottom: $ai-block-item-margin;
    max-height: $ai-params-list-height;
  }

  &__params-group {
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: auto;
  }

  &__navigation-panel {
    display: flex;
    padding: $ai-main-v-padding $ai-main-h-padding;

    background: #d9d9d9;
    border-radius: 13px;
  }
}
